define('web/routes/homes/edit', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var HomesEditRoute;

  HomesEditRoute = Ember['default'].Route.extend({
    model: function(params) {
      return this.store.find('home', params.id);
    }
  });

  exports['default'] = HomesEditRoute;

});